
.video-section {
    overflow: hidden;
    background: #fff;
}
.video-item {
    height: 3.6rem;
    overflow: hidden;
    position: relative;
}
.video-item img {
    width: 100%;
    height: 100%;
}
.video-item-title {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    padding-left: 3%;
    padding-top: 3%;
}
.video-item-title p {
    color: #fff;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 100%;
    font-size: 16px;
    text-align: left;
    font-family: '-apple-system', "Helvetica Neue", "Roboto", "Segoe UI", sans-serif;
}
.video-item-play {
    width: 0.8rem;
    height: 0.8rem;
    position: absolute;
    left: 50%;
    top: 50%;
    background-image: url(../../assets/play.png);
    background-size: contain;
    transform: translate(-50%, -50%);
}
.video-item-footer {
    height: 0.6rem;
    line-height: 0.6rem;
    text-align: left;
    padding-left: 2%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}
.video-item-footer span {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 80%;
}
.head-sculpture {
    width: .4rem;
    height: .4rem;
    overflow: hidden;
    border-radius: 50%;
    margin-right: 1.5%;
}
.head-sculpture img {
    width: 100%;
    height: 100%;
    background-size: contain;
}



