---------------------------------------- */
/* html根元素大小
---------------------------------------- */
html {
    font-size: 20vw;
}
@media screen and (max-width: 320px) {
html {
        font-size: 64px;
}
}
@media screen and (min-width: 540px) {
html {
        font-size: 108px;
}
}
body {
    max-width: 540px;
    min-width: 320px;
    margin: 0 auto;
    position: absolute;
}
#home-page {
}
.page.has-navbar .page-content {
    padding-top: 100px;
}
.cover {
    width: 100%;
    height: 100%;
}
.cover>img {
    width: 100%;
    height: 100%;
}
.nav-cell {
}
#cells {
    margin: 0;
}
#cells .row .col {
    height: 70px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}
.movie-list {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    background-color: #fff;
}
.movie-list>header {
    height: 50px;
}
.movie-list>ul {
    display: -ms-inline-flexbox;
    display: inline-flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.movie-list>ul>li {
    -ms-flex: none;
        flex: none;
    width: 49%;
    height: 2.9rem;
}
.movie-list>ul>li:nth-child(odd) {
    margin-right: 0.5%;
}
.movie-list>ul>li:nth-child(even) {
    margin-left: 0.5%;
}
.movie-list>ul>li img {
    width: 100%;
    height: 100%;
}
.movie-list .info {
    display: -ms-flexbox;
    display: flex;
    padding-left: 5%;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: start;
        align-items: flex-start;
}
.movie-list .info>p {
    color: #555;
    font-size: 15px;
    margin: 0;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    width: 100%;
    text-align: left;
}
.movie-list li>.info>span {
    color: #999999;
    font-size: 12px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    width: 100%;
    text-align: left;
}
.movie-list>header {
    padding-left: 3%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
}
.movie-list>header>span {
    display: block;
    margin-left: 10px;
    color: #000000;
    font-size: 16px;
}
.dot {
    background-image: url(../../assets/dot.png);
    background-size: 14px 14px;
    width: 14px;
    height: 14px;
    display: block;
}



